	module.exports = {"data":{"en":{"edges":[{"node":{"frontmatter":{"title":"Our Mission","picture":null,"pictureCaption":null,"figure":null},"html":"<p>No Smile Left Behind’s mission is to overcome barriers to care to promote oral health and deliver compassionate dental care for one and all.</p>\n<p>No Smile Left Behind is a state-of-the-art, self-contained dental office on wheels capable of meeting the comprehensive treatment needs of patients right where the patients happen to already be. Whether treatment is needed at schools, health fairs, summer camps, housing communities, private businesses, or other venues, No Smile Left Behind can help!</p>\n<p>No Smile Left Behind can save time, money, and hassle by arriving at locations which are already hosting the patients at the time of service. We provide convenient &#x26; excellent on-site dental care without requiring anyone to make an appointment, take time off at work, or juggle all the other demands of life just to make it to the dental office.</p>\n<p>No matter what may make getting quality dental care difficult or inconvenient, No Smile Left Behind is happy to deliver compassionate care right where it’s needed.</p>"}},{"node":{"frontmatter":{"title":"The Mobile Office","picture":null,"pictureCaption":null,"figure":[{"url":"http://res.cloudinary.com/ds0f4qyyt/image/upload/s--aZ3nUM6E--/q_jpegmini/v1495027575/No_Smile_Left_Behind_1_gkyf3h.jpg","caption":"The Mobile Office"},{"url":"http://res.cloudinary.com/ds0f4qyyt/image/upload/s--FS38MfEv--/c_scale,h_335,q_jpegmini:1,w_640/v1495027573/Rear_Treatment_Room_wkgqfm.jpg","caption":"Restorative Chair"},{"url":"http://res.cloudinary.com/ds0f4qyyt/image/upload/s--8WXvEfOU--/c_scale,h_335,q_jpegmini:1,w_640/v1495027574/Front_Treatment_Room_1_rcdjfv.jpg","caption":"Hygiene Chair"}]},"html":"<p>No Smile Left Behind's mobile office is under construction, but the finished office will look almost identical to this:</p>"}},{"node":{"frontmatter":{"title":"Dr. Nathan Becker","picture":"http://res.cloudinary.com/ds0f4qyyt/image/upload/s--1ImzCrub--/q_jpegmini/v1495031583/Nathan_geager.jpg","pictureCaption":"Nathan Becker, DDS","figure":null},"html":"<p>Dr. Nathan has spent his entire career in mobile dentistry providing compassionate care for the underserved of all ages, with a focus on children. As a result, he has developed a unique skill set and sensitivity specially tailored to achieving excellent results in prevention, education, diagnosis, &#x26; comprehensive restorative treatment among those who have difficulties accessing regular care for any reason.</p>\n<p>In his free time, Dr. Nathan enjoys reading, rock climbing, piano improvisation, cooking, meditation, and spending time outdoors.</p>"}}]},"es":{"edges":[{"node":{"frontmatter":{"title":"Nuestra Misión","picture":null,"pictureCaption":null,"figure":null},"html":"<p>No Smile Left Behind’s misión es a las barreras para promover la salud bucal y la prestación de atención dental compasiva para todos y cada uno.</p>\n<p>No Smile Left Behind es un estado-de-la-arte, la oficina dental autónoma sobre ruedas capaz de satisfacer las necesidades de tratamiento integral de los pacientes justo donde los pacientes ya están. Si el tratamiento se requiere en las escuelas, programas de salud, campamentos de verano, comunidades de vivienda, empresas privadas, u otros lugares, ¡No Smile Left Behind puede ayudar!</p>\n<p>No Smile Left Behind puede ahorrar tiempo, dinero y molestias al llegar a lugares que ya están recibiendo a los pacientes en el momento del servicio. Ofrecemos un cómodo y excelente cuidado dental en el lugar sin requerir que alguien haga una cita, tome tiempo libre en el trabajo o haga malabarismos con todas las otras demandas de la vida sólo para llegar a la oficina dental.</p>\n<p>No importa lo que puede hacer que obtener atención dental difícil o inconveniente, No Smile Left Behind se complace en ofrecer atención compasiva justo donde sea necesario.</p>"}},{"node":{"frontmatter":{"title":"La Oficina Móvil","picture":null,"pictureCaption":null,"figure":[{"url":"http://res.cloudinary.com/ds0f4qyyt/image/upload/s--aZ3nUM6E--/q_jpegmini/v1495027575/No_Smile_Left_Behind_1_gkyf3h.jpg","caption":"La Oficina Móvil"},{"url":"http://res.cloudinary.com/ds0f4qyyt/image/upload/s--FS38MfEv--/c_scale,h_335,q_jpegmini:1,w_640/v1495027573/Rear_Treatment_Room_wkgqfm.jpg","caption":"Silla restaurativa"},{"url":"http://res.cloudinary.com/ds0f4qyyt/image/upload/s--8WXvEfOU--/c_scale,h_335,q_jpegmini:1,w_640/v1495027574/Front_Treatment_Room_1_rcdjfv.jpg","caption":"Silla de higiene"}]},"html":"<p>No Smile Left Behind's oficina móvil está en construcción, pero la oficina terminada se verá casi idéntica a esto:</p>"}},{"node":{"frontmatter":{"title":"","picture":"http://res.cloudinary.com/ds0f4qyyt/image/upload/s--1ImzCrub--/q_jpegmini/v1495031583/Nathan_geager.jpg","pictureCaption":"Nathan Becker, DDS","figure":null},"html":"<p>El Dr. Nathan ha pasado toda su carrera en odontología móvil proporcionando atención compasiva a los desamparados de todas las edades, con un enfoque en los niños. Como resultado, ha desarrollado un conjunto de habilidades y sensibilidad únicas especialmente adaptadas para lograr excelentes resultados en la prevención, la educación, el diagnóstico y el tratamiento integral de restauración entre aquellos que tienen dificultades para acceder a la atención regular por cualquier motivo.</p>\n<p>En su tiempo libre, el Dr. Nathan disfruta de la lectura, la escalada en roca, la improvisación de piano, la cocina, la meditación y pasar tiempo al aire libre.</p>"}}]}},"pathContext":{}}

